

export default {
  name: 'DrawDropdown',

  props: [
    'value'
  ],

  data() {
    return {
      isDropdownShown: false,
      options: [],
      values: [],
      valuesTemp: []
    }
  },

  async created() {
    this.options = await this.$store.dispatch('draws/get')
  },

  methods: {
    reset() {
      this.valuesTemp = this.values
    },

    onDropdown(onShow) {
      this.isDropdownShown = onShow

      if (onShow) {
        this.reset()
      } else {
        let keyTemp = this.valuesTemp.sort().join('|')
        let key = this.values.sort().join('|')

        if (keyTemp !== key) {
          this.onChange()
        }
      }
    },

    onChange() {
      this.$emit('input', this.values)
      this.$emit('change')
    },

    onClear() {
      this.onChange()
      this.reset()
    },

    onRemoveTag() {
      if (this.isDropdownShown) return

      this.reset()
      this.onChange()
    }
  }
}


